@use "global" as *;


.table {
  width: 100%;
  border-collapse: collapse;  // 
  background-color: #ffffff;
}

.table__head {
  display: none;
}

.table__header {
}
.table__header-body {
}

.table__row {
  border: 1px solid #adb0b1;
}

.table__cell-head {  // 
  width: 18.34%;
  padding-block: rem(22);
  padding-left: rem(20);
  padding-right: rem(12);  // 
  background: #f1f2f5;
  font-size: rem(17);
  font-weight: $medium;

  @include mq(md) {
    width: 25%;
    padding-block: rem(15);
    padding-left: rem(10);
    padding-right: rem(6);  // 
    font-size: rem(14);
  }
}

.table__cell-body {  // 
  padding-block: rem(22);
  padding-left: rem(28);
  padding-right: rem(12);  // 
  font-size: rem(17);
  font-weight: $normal;
  line-height: 2.0;

  @include mq(md) {
    padding-block: rem(15);
    padding-left: rem(10);
    padding-right: rem(8);  // 
    font-size: rem(14);
    line-height: 1.7;
  }
}

.table__cell-body-link {
  color: #2c81d8;
  text-decoration: underline;  // 
  text-decoration-color: #2c81d8;

  font-size: rem(17);
  font-weight: $normal;
  line-height: 2.0;
  transition: opacity 0.2s ease-out;

  @include mq(md) {
    font-size: rem(14);
    line-height: 1.7;
  }
}

.table__cell-body-link:hover {  // hover
  opacity: 0.4;
}




// =======================================
// 
// =======================================
// table--sp-wide (SP) ()
.table--sp-wide {

}

.table--sp-wide .table__cell-head {  // 
  @include mq(md) {
    width: 30%;
  }
}
