@use "global" as *;


.fv {
  padding-top: rem(116);  // header (border)
  min-height: rem(900);

  @include mq(md) {
    padding-top: rem(77);  // header (border)
    min-height: auto;
  }
}

.fv__inner {
  position: relative;
}

.fv__video {
  display: block;
  width: 100%;
  object-fit: cover;
  min-height: rem(810);
  max-height: rem(810);

  aspect-ratio: 1440 / 810;
  object-fit: cover;

  @include mq(md) {
    display: none;
  }
}

.fv__video-sp {  // SP
  display: none;

  @include mq(md) {
    display: block;
    width: 100%;
    object-fit: cover;
    max-height: rem(810);

    aspect-ratio: 1440 / 810;
    object-fit: cover;
    min-height: auto;
  }
}

.fv__video-alternative-text {  //  ()
  font-size: rem(20);
  text-align: center;
  color: $color-font-base-1;

  @include mq(md) {
    font-size: rem(18);
  }
}

