@use "global" as *;


.content4 {

}

.content4__wrap {
  display: flex;
  justify-content: center;
  align-items: center;

  position: relative;
  width: 100%;

  @include mq(md) {
    display: block;
  }
}

.content4__img {
  aspect-ratio: 600 / 400;
  max-width: rem(600);
  width: 100%;
  object-fit: cover;
  position: absolute;
  left: 0;
  // z-index: -10;

  @include mq(md) {
    max-width: none;
    position: relative;
    left: auto;
  }
}

.content4__img img {
  aspect-ratio: 600 / 400;
  max-width: rem(600);
  width: 100%;
  object-fit: cover;

  @include mq(md) {
    max-width: none;
  }
}

.content4__body {
  aspect-ratio: 635 / 243;
  max-width: rem(635);
  width: 100%;
  background: $color-white;
  padding-top: rem(60);
  padding-bottom: rem(40);  // 
  padding-inline: rem(70);
  // box-shadow: rem(5) rem(6) 0 rgba(230, 233, 239, 0.6);  //  box-shadow: [] [] [] [];

  position: relative;

  margin-left: auto;
  z-index: 30;

  @include mq(md) {
    aspect-ratio: auto;
    max-width: none;
    padding-top: rem(25);
    padding-bottom: rem(20);
    padding-inline: rem(25);
  }
}

