.content4__shadow-left {  //   z-index.content4__wrap()
  aspect-ratio: 635 / 243;
  max-width: rem(635);
  width: 100%;
  height: 100%;
  background: rgba(230, 233, 239, 0.6);
  mix-blend-mode: multiply; // 

  position: absolute;
  bottom: rem(-6);
  left: rem(5);
  z-index: 10;

  @include mq(md) {
    bottom: rem(-4);
    left: rem(3);

    display: none;
  }
}

.content4__shadow-right {  //   z-index.content4__wrap()
  aspect-ratio: 635 / 243;
  max-width: rem(635);
  width: 100%;
  height: 100%;
  background: rgba(230, 233, 239, 0.6);
  mix-blend-mode: multiply; // 

  position: absolute;
  bottom: rem(-6);
  right: rem(-5);
  z-index: 10;

  @include mq(md) {
    bottom: rem(-4);
    right: rem(-3);

    display: none;
  }
}

.content4__title {
  text-align: center;
  font-size: rem(22);
  font-weight: $bold;
  color: #181818;
  padding-bottom: rem(18);
  border-bottom: 1px solid #686f81;

  @include mq(md) {
    font-size: rem(18);
    padding-bottom: rem(14);
  }
}

.content4__text {
  margin-top: rem(30);
  font-size: rem(17);
  font-weight: $normal;
  color: $color-font-base-1;
  line-height: 1.8;
  text-align: left;

  @include mq(md) {
    margin-top: rem(20);
    font-size: rem(14);
    line-height: 1.75;
  }
}

.content4__btn {
  margin-top: rem(35);
  display: block;
  width: fit-content;
  margin-left: auto;

  @include mq(md) {
    margin-top: rem(10);
  }
}




